body {
  font-family: monospace;
}

.filter-invert {
  filter: invert(100%);
}

.logo {
  width: 48px;
  height: 48px;
  margin: 12px;
  /* opacity: 0.5; */
  transition: all 0.2s;
}

.logo:hover {
  /* opacity: 1.0; */
  width: 58px;
  height: 58px;
}

.skills-tile {
  background-color: rgba(180, 233, 161, 0.03);
  padding: 8px 8px 0px 8px;
  width: 100px;
  border-radius: 15px;
  box-shadow: 4px 2px 4px #000000;
}

.card {
  box-shadow: 4px 2px 4px #000000;
}
